import React, { useMemo } from 'react'
import { useListPolicyModules } from '@aserto/console-common'

import ModulesTable from '../../../../components/policies/ModulesTable'
import { useActiveConfigurationName } from '../../../../services/ActiveConfiguration'

const Modules: React.FC = () => {
  const { configurationName } = useActiveConfigurationName()
  const {
    data: listPolicyModules,
    isLoading,
    error,
  } = useListPolicyModules({ authorizerInstance: configurationName! }, 'id,package_path,raw')
  const policyModules = useMemo(() => {
    if (!listPolicyModules || !listPolicyModules.result) {
      return
    }
    return listPolicyModules.result
      .map((module) => {
        return {
          id: module.id,
          package_path: module.package_path?.replace('data.', ''),
          raw: module.raw,
        }
      })
      .sort((a, b) => (a.package_path! < b.package_path! ? -1 : 1))
  }, [listPolicyModules])

  const fetchContent = (moduleId: string) => {
    return policyModules?.find((module) => module.id === moduleId)?.raw || ''
  }

  return (
    <ModulesTable
      fetchContent={fetchContent}
      isLoading={isLoading || !!error}
      policyModules={policyModules!}
    ></ModulesTable>
  )
}

export default Modules
