import React, { useEffect, useState } from 'react'
import { Nav } from 'react-bootstrap'
import { Highlight, SpinnerToggle, useDeviceDetect } from '@aserto/console-common'

import diagram from '../../../assets/diagram.svg'
import { v2ApiModule } from '../../../types/local/authorizer'
import Frame from '../../../views/PolicyInstance/Frame'
import {
  Column,
  Content,
  ContentHeader,
  Diagram,
  MobileColumn,
  PendingBundleContainer,
  PillLineItem,
} from './styles'

interface ModulesTableProps {
  policyModules: v2ApiModule[]
  fetchContent: (moduleId: string) => string
  isLoading: boolean
}

const ModulesTable: React.FC<ModulesTableProps> = ({ policyModules, fetchContent, isLoading }) => {
  const [isModalOpenOnMobile, setIsModalOpenOnMobile] = useState(false)
  const { isMobile } = useDeviceDetect()
  const [moduleId, setModuleId] = useState<string | undefined>()
  const content = fetchContent(moduleId!)
  const policyInstance = {
    source_url: '',
  }

  useEffect(() => {
    if (!policyModules || policyModules.length === 0) {
      return
    }

    const policyModuleId = policyModules[0].id
    if (!moduleId && !!policyModuleId) {
      setModuleId(policyModuleId)
    }
  }, [moduleId, policyModules])

  const LeftSideItems = () => {
    if (!policyModules || policyModules.length === 0) {
      return null
    }

    return (
      <>
        {policyModules?.map((p) => (
          <PillLineItem key={p.id}>
            <Nav.Link className="light-pills" eventKey={p.id}>
              <span>{p.package_path}</span>
            </Nav.Link>
          </PillLineItem>
        ))}
      </>
    )
  }

  if (isLoading) {
    return <Frame />
  }

  return (
    <Frame>
      {!policyModules ? (
        <PendingBundleContainer>
          <h4>Hey, where's my policy?</h4>
          <div>
            Aserto is building your policy, pushing it to the registry, and triggering the
            authorizer to pull it. The process typically takes about 30 seconds. This is what
            happens:
          </div>
          <Diagram src={diagram} />
          <h4>What to do next</h4>
          <ul>
            <li>
              Click the Refresh button at the top right of this page to see if there are any errors
              from the authorizer.
            </li>
            <li>
              Check that{' '}
              <a
                href={`${policyInstance?.source_url}/actions`}
                rel="noopener noreferrer"
                target="_blank"
              >
                the Action on the policy repository
              </a>{' '}
              built an image.
            </li>
            <li>Verify the Policy settings (left side of this screen).</li>
          </ul>
        </PendingBundleContainer>
      ) : (
        <>
          <Column style={{ marginLeft: 20 }}>
            <ContentHeader>Module Name</ContentHeader>
            <Content $flex $paddingTop={18}>
              <Nav
                activeKey={moduleId!}
                className="flex-column"
                defaultActiveKey={moduleId!}
                variant="pills"
                onSelect={(key) => {
                  setModuleId(key ?? undefined)
                  setIsModalOpenOnMobile(true)
                }}
              >
                <LeftSideItems />
              </Nav>
            </Content>
          </Column>
          <MobileColumn
            show={isModalOpenOnMobile}
            onCloseMobile={() => setIsModalOpenOnMobile(false)}
          >
            <>
              <ContentHeader shouldPadLeftWhenMobile>Definition</ContentHeader>
              <Content $flex $hasBorderLeft $paddingTop={18}>
                {isMobile ? <SpinnerToggle /> : <Highlight language="rego">{content}</Highlight>}
              </Content>
            </>
          </MobileColumn>
        </>
      )}
    </Frame>
  )
}

export default ModulesTable
